import React, { Component } from 'react';
import Auxi from '../../hoc/Auxi';
import Sbar from '../../containers/Sbar/Sbar';
import Control from './Control'


class Main extends Component {
  state = {
    degree:1
  };

  handleControlAdd=() => {
    const oldd = this.state.degree;
    const newd = oldd+1;
    this.setState({degree:newd})
  };

  handleControlSub=() => {
    const oldd = this.state.degree;
    const newd = oldd-1;
    this.setState({degree:newd})
  };

  

  render() {
    console.log(this.props);
    return (
      <Auxi>
          <Sbar />
          <Control 
          degree={this.state.degree}
          add={this.handleControlAdd}
          sub={this.handleControlSub} />
          
      </Auxi>
    );
  }
}

export default Main;



