import React from 'react';
import classes from './Tool.css';
import Link from './About/Link';
const tool = (props) => (
    <ul className = {classes.Tool}>
        <Link link="/home">Search</Link>
        
        <Link link="/about">About</Link>
    </ul>
);

export default tool;

