import React, { Component } from 'react';

import Layout from './components/Layout/Layout';


import {BrowserRouter} from 'react-router-dom';

class App extends Component {
  state = {
    degree:1
  };

  handleControlAdd=() => {
    const oldd = this.state.degree;
    const newd = oldd+1;
    this.setState({degree:newd})
  };

  handleControlSub=() => {
    const oldd = this.state.degree;
    const newd = oldd-1;
    this.setState({degree:newd})
  };

  render() {
    return (
      <BrowserRouter>
        <Layout degree={this.degree}/>
        <p>{window.token}</p>
      </BrowserRouter>  
    );
  }
}

export default App;



