import React from 'react';
import classes from './Link.css';
import { NavLink } from 'react-router-dom';
const Link = (props) => (
    <li className = {classes.Link}>
        <NavLink 
            exact
            activeClassName={classes.active}
            to={props.link}
            >{props.children}</NavLink>
    </li>
);

export default Link;


