import React , {Component} from 'react';
import axios from 'axios';
import Tree from 'react-tree-graph';
import Modal from './Modal';

import classes from './Sbar.css';

class Sbar extends Component {
    state = {
        searchValue: '',
        partners: '',
        value: '1',
        clicked:0,
        clickedProtein:{name:'',
                        children:[]}
    };

    handleOnChange = event => {
        this.setState({ searchValue: event.target.value });
    };

    nodeClick = (event,node) =>{
        event.preventDefault();
        const data = {"data":[
            {"name":node,
            "children":[]},
            {"value":'1'}]
        };
        console.log(data);
        axios.post('https://ezyprotein.herokuapp.com/home',data)
            .then(response=>{
                console.log(response);
                this.setState({clickedProtein:{name:'',children:[]}});
                this.setState({clickedProtein:response.data});
                
            })
        this.setState({clicked:1});
    }

    nodeClose = () =>{
        this.setState({clicked:0});
    }

    
    handleSubmit = (event) => {
        event.preventDefault();
        const data = {"data":[
            {"name":this.state.searchValue,
            "children":[]},
            {"value":this.state.value}]
        };
        axios.post('https://ezyprotein.herokuapp.com/home',data)
            .then(response=>{
                this.setState({partners:''});
                this.setState({partners:response.data});
                console.log(response)
            })
    }

    handleSelect = (event) => {
        this.setState({value: event.target.value});
    };





    render() {
        return (
            <div className={classes.content}>
                <h1>Welcome to the app</h1>
                <form onSubmit={this.handleSubmit.bind(this)}>
                    <input 
                    name="text"
                    type="text" 
                    placeholder="Please enter protein name" 
                    onChange={this.handleOnChange.bind(this)}
                    value={this.state.searchValue}
                    />
                    <label>
                    Pick the degree of propagation:
                    <select value={this.state.value} onChange={this.handleSelect}>
                        <option value='1'>1</option>
                        <option value='2'>2</option>
                        <option value='3'>3</option>
                        <option value='4'>4</option>
                    </select>
                    </label>
                    <div>
                        <input type="submit" value="Search"></input>
                    </div>
                </form>
                    {this.state.partners ? (
                    <div className={classes.tree}>
                        <Tree 
                        animated
                        nodeRadius={15}
                        gProps={{
                            className:classes.node,
                            onClick:this.nodeClick
                        }}
                        pathProps={{className:classes.link}}
                        data={this.state.partners}
                        height={1500*this.state.value}
                        width={1000}/>
                    </div>
                    ) : (
                    <p>Protein not Found, Please try another one.</p>
                    )}

                    <Modal protein={this.state.clickedProtein} show={this.state.clicked} modalClosed={this.nodeClose}/>
               
                    
            </div>
            
        );
    }
}

export default Sbar;


