import React from 'react';
import classes from './Control.css';


const Control = (props) => (
    <div className={classes.control}>
        <div className={classes.controls}>
            <div className={classes.Label}>Degrees of Propagation: {props.degree} </div>
            <button disabled={props.degree<=1} className={classes.Less} onClick={props.sub}>Less</button>
            <button className={classes.More} onClick={props.add}>More</button>
        </div>
    </div>
);

export default Control;


